
<script>import Editor from "./Editor.svelte";
import Preview from "./Preview.svelte";
import { css } from "emotion";
export let defaultCode;
export let deps;
export let width = 450;
export let height = 300;
export let orientation = "horizontal";
let code = defaultCode;

function handleInput(e) {
  code = e.detail.code;
}

const editorWrapperStyle = orientation === "horizontal" ? css`
                  margin-left: 5px;
              ` : css`
                  margin-top: 5px;
              `;
const previewWrapperStyle = css`
        flex: 1;
        padding: 10px;
        box-shadow: 0 2px 8px #f0f1f2;
        border: 1px solid #eaeefb;
        ${orientation === "vertical" ? `display: flex` : ""}
    `;
const containerStyle = css`
        display: flex;
        width: 90%;
        flex-direction: ${orientation === "vertical" ? "column" : "row"};
    `;</script>


<div class={containerStyle}>
        <div class={previewWrapperStyle}>
            <Preview code={code} deps={deps} />
        </div>
        <div class={editorWrapperStyle}>
            <Editor language="tsx" defaultCode={defaultCode} width={orientation === "horizontal" ? width : "auto"} height={height} on:input={handleInput}></Editor>
        </div>
    </div>


<style>

</style>