import Notification from "./Notification.svelte";
export function notification(config) {
  const {
    title = "",
    message = "",
    duration = 3000,
    type
  } = config || {};
  const div = document.createElement("div");
  document.body.appendChild(div);
  const className = ".el-notification";
  const instances = document.querySelectorAll(className);
  const lastInstance = instances[instances.length - 1];
  const top = lastInstance ? lastInstance.getBoundingClientRect().bottom + 16 : 16;
  let component = Reflect.construct(Notification, [{
    target: div,
    props: {
      top,
      title,
      message,
      type
    },
    intro: true
  }]);

  const removeNotification = () => {
    if (component) {
      component.$destroy();
      component = null;
    }
  };

  component.$on("close", () => {
    removeNotification();
  });
  component.$on("willUnmount", e => {
    const {
      height,
      top
    } = e.detail;
    const instances = document.querySelectorAll(className);

    for (let i = 0; i < instances.length; ++i) {
      const element = instances[i];
      const rect = element.getBoundingClientRect();

      if (rect.top > top) {
        element.style.setProperty("top", (rect.top - height - 16).toString() + "px");
      }
    }

    document.body.removeChild(div);
  });
  setTimeout(() => {
    removeNotification();
  }, duration);
}