
<script>import { onMount, onDestroy } from "svelte";
import { compileComponent } from "./compiler";
import { debounce } from "./debounce";
import Loading from "../component/loading/Loading.svelte";
import { css } from "emotion";
import { notification } from "../component/notification/index";
export let code;
export let deps = {};
let component = null;
let container = null;
let mounted = false;
let isReady = false;
const refreshComponent = debounce((code, deps = {}) => {
  createComponent();
}, 250);
const notify = debounce(config => notification(config), 1000);

$: {
  if (mounted) {
    refreshComponent(code, deps);
  }
}

onMount(() => {
  mounted = true;

  window.onerror = message => {
    notify({
      title: "Runtime Error",
      message: message.toString(),
      type: "error",
      duration: 5000
    });
    return true;
  };
});
onDestroy(() => {
  destroyComponent();
});

function destroyComponent() {
  if (component) {
    component.$destroy();
    component = null;
  }
}

function createComponent() {
  let boundary;
  let newComponent;

  try {
    const Component = compileComponent(code, deps);

    if (Component) {
      boundary = document.createElement("div");
      newComponent = new Component({
        target: boundary
      });
    }
  } catch (error) {
    const message = `create component failed, error: ${error.message}`;
    notify({
      title: "Compile Error",
      message,
      type: "error",
      duration: 5000
    });
    return;
  }

  destroyComponent();
  component = newComponent;
  isReady = true;
  container.appendChild(boundary.firstChild);
}</script>


<div bind:this={container} style="width:100%; height:100%">
        {#if !isReady}
            <Loading className={css`
                    min-height: 300px;
                `} />
        {/if}
    </div>


